
import Vue from 'vue';
import VueRouter from 'vue-router';

import App from './App';

const Home = () => import('./pages/Home');
const About = () => import('./pages/About');

Vue.use(VueRouter);

const router = new VueRouter({
  mode: 'history',
  routes: [
    { path: '/', component: Home },
    { path: '/about', component: About },
  ],
});

new Vue({
  router,
  render: h => h(App),
}).$mount('#app');



